var qualityScore = "!\"#$%&'()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[\]^_`abcdefghijklmnopqrstuvwxyz{|}~";

exports.fragmentByLine = function (arr) {
  return arr.map(function (str) {
    arr = str.split('\n');
    return {
      data: arr[1],
      score: arr[2]
    };
  });
};

exports.filterByLength = function (arr, minLength) {
  return arr.filter(function (x) {
    return x.data.length > minLength;
  });
}; // Filter by quality, and return objects with 


exports.filterByQuality = function (arr, minQuality) {
  var rtn = [];
  arr.map(function (x) {
    var total = 0;

    for (var i = 0; i < x.score.length; i++) {
      total += qualityScore.indexOf(x.score[i]);
    }

    var quality = total / x.score.length;

    if (quality >= minQuality) {
      rtn.push({
        data: x.data,
        quality: quality,
        score: x.score
      });
    } else {
      console.log('failed the quality test by ', quality, minQuality);
    }
  });
  return rtn;
};